.subscription {
  padding: 120px 0;
  background-color: $color-ghost-white;

  @include vp-1200 {
    padding: 100px 0;
  }

  @include vp-767 {
    padding: 64px 0 70px;
  }
}

.subscription .container {
  position: relative;
  text-align: center;

  @include vp-1200 {
    padding: 0 163px;
  }

  @include vp-767 {
    padding: 0 15px;
  }
}

.subscription__title {
  margin: 0;
  margin-bottom: 5px;
  padding: 0;
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 65px;
  letter-spacing: 0.03em;
  text-align: center;

  @include vp-1200 {
    margin-left: -15px;
    margin-bottom: 9px;
    font-size: 42px;
    line-height: 50px;
  }

  @include vp-767 {
    margin-bottom: 10px;
    margin-left: -10px;
    font-size: 32px;
    line-height: 38px;
  }
}

.subscription__content {
  margin: 0;
  padding: 0;
}

.subscription__item {
  list-style-type: none;
}

.subscription__image {
  display: block;
  width: 466px;
  height: 494px;
  position: absolute;
  bottom: -270px;
  right: -180px;

  @include vp-1200 {
    width: 423px;
    height: 443px;
    bottom: -224px;
    right: -76px;
  }

  @include vp-767 {
    display: none;
  }
}
